.board {
    margin: auto;
    background: #ddd;
    overflow: hidden;
    outline: none;
    border: 2px solid blue
}

.cell, .body-cell, .food-cell  {
  width: 30px;
  height: 30px;
  float: left;
  overflow: hidden;
  box-sizing: border-box;
  border: 1px solid blue
}

.body-cell {
  background-color: blue;
}

.food-cell {
  background-color: red;
}